import {ApiService} from "@/api/ApiService";
import {createStore} from "vuex";

export const api = new ApiService()

const store = createStore({
    state: {
        serviceList: [],
    },
    getters: {},
    mutations: {
        updateServiceList(state, serviceList) {
            state.serviceList = serviceList || [];
        },
    },
    actions: {
        getServiceList(context) {
            context.commit('updateServiceList', [
                {label: "avado de Mantenimiento", id: 1},
                {label: "Limpieza Integral", id: 2},
                {label: "Detailing Interior", id: 3},
                {label: "Tratamiento cerámico de Cuero", id: 4},
                {label: "Tratamiento Cerámico de Cristales (antilluvia)", id: 5},
                {label: "Tratamiento Cerámico de llantas", id: 6},
                {label: "Tratamiento Cerámico de plasticos", id: 7},
                {label: "Tratamiento de Cera Carnauba", id: 8}
            ]);
            /*return api.get(`/service/`)
                .then(response => {
                    context.commit('updateServiceList', response.data.services);

                    return response.data;
                });*/
        },
        getServiceItem(context, id) {
            return api.get(`/service/${id}`)
                .then(response => {
                    return response.data;
                });
        },
        getPortfolioItem(context, id) {
            return api.get(`/portfolio/${id}`)
                .then(response => {
                    return response.data;
                });
        },
    },
    modules: {}
})

export default store;
